import { baseURL, imgURl } from '../../common/config/index.js';
import { addProduct, getProductCategory, getShopProductById, updProduct } from '@/request/API/index.js';
export default {
	data() {
		return {
			isShowshangj: true,
			isshowBtn: true,
			categoryName: '',
			currentTabs: -1,
			categoryList: [], //分类列表
			productName: '', //商品名
			categoryId: '', //商品分类id
			descript: '', //商品描述
			productImage: [],
			price: '', //价格：此为商品展示价格
			sellCount: '', //商品数量
			specUnit: '', //商品单位
			expressId: 0, //配送方式
			guigeList: [
				{
					productSpecs: '', //名称
					price: '', //价格
					specForm: 1 //规格来源 1 店铺添加
				}
			],
			imglist: [],
			radio2: 1, //折扣
			isshow: false, //商品属性
			productId: ''
		};
	},
	onLoad(options) {
		if (options.id) {
			this.productId = options.id;
			this.getShopProductById(options.id);
		}
		this.getProductCategory(1, 1);
	},
	computed: {
		...mapState(['userId', 'shopId'])
	},
	methods: {
		//获取产品信息
		getShopProductById(id) {
			getShopProductById(id).then(res => {
				if (res.data.code == 0) {
					let list = [];
					for (let item of res.data.data.specList) {
						let params = {
							productSpecs: item.productSpecs, //名称
							price: item.price, //价格
							specForm: 1 //规格来源 1 店铺添加
						};
						list.push(params);
					}
					this.guigeList = list;
					this.price = res.data.data.price;
					this.productName = res.data.data.productName;
					this.descript = res.data.data.descript;
					this.categoryId = res.data.data.categoryId;
					this.sellCount = res.data.data.sellCount;
					this.specUnit = res.data.data.specUnit;
					this.expressId = res.data.data.expressId;
					// this.productImage = res.data.data.imgList;
				}
			});
		},
		//产品规格选择
		categoryChange(index, categoryId, name) {
			this.currentTabs = index;
			this.categoryId = categoryId;
			this.categoryName = name;
		},
		getProductCategory(level, type) {
			getProductCategory(level, type).then(res => {
				if (res.data.code == 0) {
					this.categoryList = res.data.data.cateGories;
				}
			});
		},
		AddProduct() {
			if (!this.productName) {
				uni.showModal({
					title: '',
					content: '请输入商品名',
					showCancel: false
				});
				return;
			}
			if (this.productImage.length == 0) {
				uni.showModal({
					title: '',
					content: '请上传商品图片',
					showCancel: false
				});
				return;
			}
			if (!this.price) {
				uni.showModal({
					title: '',
					content: '请输入商品价格',
					showCancel: false
				});
				return;
			}
			if (!this.categoryId) {
				uni.showModal({
					title: '',
					content: '请选择商品分类属性',
					showCancel: false
				});
				return;
			}
			//删选删除添加的空规格
			for (let i = 0; i < this.guigeList.length; i++) {
				if (this.guigeList[i].productSpecs == '') {
					this.guigeList.splice(i);
				}
			}
			if (this.guigeList.length == 0) {
				uni.showModal({
					title: '',
					content: '请至少上传一种商品规格',
					showCancel: false
				});
				return;
			}
			if (!this.specUnit) {
				uni.showModal({
					title: '',
					content: '请输入商品单位',
					showCancel: false
				});
				return;
			}
			if (!this.sellCount) {
				uni.showModal({
					title: '',
					content: '请输入商品库存',
					showCancel: false
				});
				return;
			}

			if (!this.descript) {
				uni.showModal({
					title: '',
					content: '请输入商品描述',
					showCancel: false
				});
				return;
			}

			let params = {
				userId: this.userId,
				shopId: this.shopId,
				productName: this.productName,
				categoryId: this.categoryId,
				sellCount: this.sellCount,
				price: this.price,
				productImage: this.productImage.join(','),
				descript: this.descript,
				specUnit: this.specUnit,
				expressId: this.expressId,
				productSpecDtoList: this.guigeList
			};
			this.isShowshangj = false;
			addProduct(params).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '添加成功'
					});
				}
				setTimeout(res => {
					uni.navigateBack({
						delta: 1
					});
				}, 2000);
			});
		},
		//立即修改
		AddProduct2() {
			if (!this.productName) {
				uni.showModal({
					title: '',
					content: '请输入商品名',
					showCancel: false
				});
				return;
			}
			if (!this.sellCount) {
				uni.showModal({
					title: '',
					content: '请输入商品库存',
					showCancel: false
				});
				return;
			}
			if (!this.price) {
				uni.showModal({
					title: '',
					content: '请输入商品价格',
					showCancel: false
				});
				return;
			}
			if (!this.descript) {
				uni.showModal({
					title: '',
					content: '请输入商品描述',
					showCancel: false
				});
				return;
			}
			if (!this.specUnit) {
				uni.showModal({
					title: '',
					content: '请输入商品单位',
					showCancel: false
				});
				return;
			}
			if (!this.categoryId) {
				uni.showModal({
					title: '',
					content: '请选择商品分类',
					showCancel: false
				});
				return;
			}
			if (this.productImage.length == 0) {
				uni.showModal({
					title: '',
					content: '请上传商品图片',
					showCancel: false
				});
				return;
			}
			//删选删除添加的空规格
			for (let i = 0; i < this.guigeList.length; i++) {
				if (this.guigeList[i].productSpecs == '') {
					this.guigeList.splice(i);
				}
			}
			let params = {
				productId: this.productId,
				userId: this.userId,
				shopId: this.shopId,
				productName: this.productName,
				categoryId: this.categoryId,
				sellCount: this.sellCount,
				price: this.price,
				productImage: this.productImage.join(','),
				descript: this.descript,
				specUnit: this.specUnit,
				expressId: this.expressId,
				productSpecDtoList: this.guigeList
			};
						this.isShowshangj = false;
			updProduct(params).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '修改成功'
					});
				}
				setTimeout(res => {
					uni.navigateBack({
						delta: 1
					});
				}, 2000);
			});
		},
		addGuige() {
			if (this.guigeList.length > 5) {
				return;
			}
			let params = {
				productSpecs: '', //名称
				price: '', //价格
				specForm: 1 //规格来源 1 店铺添加
			};
			this.guigeList.push(params);
		},
		paizhao() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: res => {
					console.log(res);
					this.imglist.push(...res.tempFilePaths);
					uni.uploadFile({
						url: baseURL + '/file/upload',
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							user: 'test',
							file: res.tempFilePaths[0]
						},
						success: uploadFileRes => {
							let aaa = JSON.parse(uploadFileRes.data);
							if (aaa.code == 0) {
								console.log(uploadFileRes);
								this.productImage.push(aaa.data);
								uni.showToast({
									title: '上传成功'
								});
							}
						}
					});
				}
			});
		},
		radio1Change() {
			if (this.expressId == 0) {
				this.expressId = 1;
			} else {
				this.expressId = 0;
			}
		},
		radio2Change() {
			if (this.radio2 == 1) {
				this.radio2 = 2;
			} else {
				this.radio2 = 1;
			}
		},
		ishowChange() {
			this.isshow = true;
		},
		isOk() {
			console.log(888);
			this.isshow = false;
		}
	}
};
